import {
	mapGetters,
	mapMutations
} from "vuex"
export default {
	data() {
		return {
			addressList: [],
			addressScop: false
		};
	},
	computed:{
		...mapGetters({
			getAddressState:'getAddressState',
		})
	},
	onShow() {
		this.getAddress()
	},
	onLoad() {
		this.getAddress()
	// 	uni.getSetting({
	// 		success: (res) => {
	// 			console.log(res)
	// 			console.log(res.authSetting['scope.address'])
	// 			if(res.authSetting['scope.address']){
	// 				console.log('已打开授权')
	// 			}else{
	// 				uni.openSetting({
	// 					success: (res) => {
	// 						console.log(res)
	// 					}
	// 				})
	// 			}
	// 		}
	// 	})
	},
	methods: {
		// vuex设置收货地址
		...mapMutations({
			setAddressData:'setAddressData'
		}),
		// 选择收货地址并返回订单详情页面
		selecteAddress(data){
			if(this.getAddressState == 0){
				return
			}
			this.setAddressData(data);
			// 返回上一页
			uni.navigateBack({
				delta:1
			})
		},
		// 获取微信的收货地址
		getWechatAddress() {
			uni.chooseAddress({
				success: (res) => {
					console.log(res)
					this.addressScop = true
					this.$api.addressAdd({
						name: res.userName,
						phone: res.telNumber,
						address: res.provinceName + ' ' + res.cityName + ' ' + res.countyName,
						house: res.detailInfo,
						is_confirm: 2
					}).then(res => {
						if(res.data.errno === 0){
							console.log('我选择了微信里的收货地址')
							this.getAddress()
						}else{
							console.log('存储到数据库失败了')
						}
					})
				},
				fail: (err) => {
					console.log(err)
					// 打开一个提示弹窗
					if (this.addressScop === false) {
						uni.showModal({
							content: '检测到您没打开获取地址功能权限，是否去设置打开？',
							confirmText: "确认",
							cancelText: '取消',
							success: (cRes) => {
								console.log(cRes)
								if (cRes.confirm) {
									// 用户点击确定后打开微信原生设置页面
									uni.openSetting({
										success: (oRes) => {
											console.log(oRes);
											this.addressScop = oRes.authSetting['scope.address']
										}
									})
									this.addressScop = true
								} else {
									console.log('取消');
								}
							}
						})
					}
				}
			})
		},
		// 获取收货地址
		getAddress(){
			this.$api.addressList().then((res) => {
				console.log(res)
				if(res.data.errno === 0){
					this.addressList = res.data.data
					console.log(this.addressList)
				}else if(res.data.errno === 400 || res.data.errno === 4008 || res.data.errno === 4009){
					uni.showToast({
						title: '登录过时，请重新登录',
						duration: 1000,
						icon: 'none'
					})
					uni.setStorageSync('user_info', {
						token: '',
						user_id: '',
						user_img: '../../static/img/mine/default-img.png',
						user_name: '点击头像登录'
					});
					
					setTimeout(() => {
						uni.switchTab({
							url: '/pages/mine/mine'
						})
					},1000)
				}
			})
		},
		// 编辑收货地址
		toEditAddress(info) {
			// 将当前选择的地址信息存入缓存
			uni.setStorageSync('address',{
				id: info.address_id,
				name: info.name,
				phoneNum: info.phone,
				address: info.address,
				detailAddress: info.house
			})
			// 跳转到编辑页面
			uni.navigateTo({
				url: '/pages/mine/address/editAddress'
			})
		},
		// 新增收货地址
		toAddAddress() {
			uni.navigateTo({
				url: '/pages/mine/address/addAddress'
			})
		}
	}
}
